<template>
<div>
<loading :active.sync="isLoading"></loading>
<div class="container">


<div class="row justify-content-center">
<div class="col-md-8"v-if="FollowProduct.length !==0">
<h2 class="text-center mb-5 font-weight-bold"><i class="far fa-grin-hearts text-danger"></i></h2>
<table class="table table-borderless bg-dark1">
<thead class="bg-dark">
<tr>
<th></th>
<th></th>
<th></th>
<th ></th>
<th></th>
</tr>

</thead>
<tbody>
<tr class="trBorder" v-for="item in FollowProduct">
<td>{{item.title}}</td>
<td>{{item.category}}</td>
<td>NT$ {{item.price}}</td>
<td class="tdAddCart" @click="addcart(item)"><i class="fas fa-shopping-cart fa-2x text-white1"></i></td>
<td class="tdDelTrack"><i class="fas fa-trash text-danger fa-2x"@click="deletee(item.id)"></i></td>
</tr>

</tbody>




</table>
</div>
<div class="col-md-8" v-else>

<h1 class="text-center text-dark1 mb-5 font-weight-bold"><i class="far fa-sad-tear text-danger"></i>,</h1>
<div class="text-center mb-5 pt-5">
<a class="h3 cartin font-weight-bold text-dark1"href="#/product"><i class="fas fa-arrow-circle-left"></i></a> 
</div>


</div>
</div>


<h2 class="text-center text-white p-3 bg-dark1 mt-5 mb-5 font-weight-bold"><i class="fas fa-utensils"></i></h2>
<!---->
<div class="row ">
<!---->
<div class="col-md-4 text-center  mb-3">
<a href="#"@click.prevent="toCategory('burger')">
<img src="https://upload.cc/i1/2020/12/16/bTHgpY.jpg" class="circle mb-4 img-fluid" alt="">
</a>
<h2 class="font-weight-bold"></h2>
<p class="hamP1">!</p>
<p class="hamP2"></p>
<p class="hamP3"></p>
<button class="btn btn-outline-dark"@click.prevent="toCategory('burger')"></button>
</div> 
<!---->
<div class="col-md-4 text-center mb-3">
<a href="#"@click.prevent="toCategory('dessert')">
<img src="https://upload.cc/i1/2020/12/16/HtSnj8.jpg" class="circle mb-4 img-fluid" alt="">
</a>
<h2 class="font-weight-bold"></h2>
<p class="dashP1">!</p>
<p class="dashP2">9()</p>
<p class="dashP3"></p>
<button class="btn btn-outline-dark"@click.prevent="toCategory('dessert')"></button>
</div>
<!---->
<div class="col-md-4 text-center mb-3">
<a href="#"@click.prevent="toCategory('drink')">
<img src="https://upload.cc/i1/2020/12/16/eiLXZl.jpg" class="circle mb-4 img-fluid" alt="">
</a>
<h2 class="font-weight-bold"></h2>
<p class="drinkP1">!</p>
<p class="drinkP2">Bottomless Beverages  ()</p>
<p class="drinkP3"></p>
<button class="btn btn-outline-dark"@click.prevent="toCategory('drink')"></button>
</div>

</div>
</div>
</div>

</template>

<script>
export default {
    data() {
        return {
             trackData:JSON.parse(localStorage.getItem('tableData')) || [],
             cartData:JSON.parse(localStorage.getItem('cartData')) || [],
             products:[],
             isLoading:false,
             FollowProduct:[],
             
        }
    },
    methods: {
      getproducts(){
            const api =`${process.env.APIPATH}/api/${process.env.MEPATH}/products/all`;
             const vm = this;
            vm.isLoading=true;
           
              vm.$http.get(api).then((response) => {
            
            vm.isLoading = false;
            vm.products = response.data.products;
           vm.getFollow();
           
          
            
            
          });

      },
      //
        toCategory(Name){
      this.$router.push({
        name:'customP',
        params:{categoryname:Name},
      });
    },
      getFollow(){
          const vm = this;
          vm.FollowProduct = vm.products.filter(
              item => vm.trackData.indexOf(item.id) > -1,
          )

      },
       deletee(id){
            const vm =this;
           const followId = vm.trackData.indexOf(id);
           if(followId !==-1 ){
             vm.trackData.splice(followId,1);
             vm.getFollow();
           vm.$bus.$emit('message:push','','light');
           }
           localStorage.setItem('tableData',JSON.stringify(vm.trackData));
          
          

       },
          addcart(data){
          const vm = this;
          const cacheCarID = [];
          vm.cartData.forEach((item) => cacheCarID.push(item.product_id));
         
          if (cacheCarID.indexOf(data.id) === -1) {
         
            const cartContent = {
            product_id:data.id,
            qty:1,
            title:data.title,
            price:data.price,
           imageUrl:data.imageUrl,
          };
          vm.cartData.push(cartContent);
          localStorage.setItem('cartData',JSON.stringify(vm.cartData));
          }else{
            let cache={};
           vm.cartData.forEach( (item,keys) =>{
             if(item.product_id ===data.id){
               
                cache={
                 product_id:data.id,
                 qty:item.qty+=1,
                 title:data.title,
                 price:data.price,
                 imageUrl:data.imageUrl,
               };
               vm.cartData.splice(keys,1);
             
              
             }
           });
             vm.cartData.push(cache);
               localStorage.setItem('cartData',JSON.stringify(vm.cartData));
               
          }
          
          vm.$bus.$emit('message:push','','light');
         
          
        },
     
  
    },
    created() {
      this.getproducts();
    },
}
</script>


